export var SET_CURRENT_USER = "SET_CURRENT_USER";
export var GOOGLE_SIGN_IN_START = "GOOGLE_SIGN_IN_START";
export var SIGN_IN_SUCCESS = "SIGN_IN_SUCCESS";
export var SIGN_IN_FAILURE = "SIGN_IN_FAILURE";
export var EMAIL_SIGN_IN_START = "EMAIL_SIGN_IN_START";
export var CHECK_USER_SESSION = "CHECK_USER_SESSION";
export var SIGN_OUT_START = "SIGN_OUT_START";
export var SIGN_OUT_SUCCESS = "SIGN_OUT_SUCCESS";
export var SIGN_OUT_FAILURE = "SIGN_OUT_FAILURE";
export var SIGN_UP_START = "SIGN_UP_START";
export var SIGN_UP_SUCCESS = "SIGN_UP_SUCCESS";
export var SIGN_UP_FAILURE = "SIGN_UP_FAILURE";
export var googleSignInStart = function () { return ({
    type: "GOOGLE_SIGN_IN_START"
}); };
export var signInSuccess = function (user) { return ({
    type: "SIGN_IN_SUCCESS",
    payload: user
}); };
export var signInFailure = function (error) { return ({
    type: "SIGN_IN_FAILURE",
    payload: error
}); };
export var emailSignInStart = function (emailAndPassword) { return ({
    type: "EMAIL_SIGN_IN_START",
    payload: emailAndPassword
}); };
export var checkUserSession = function () { return ({
    type: "CHECK_USER_SESSION"
}); };
export var signOutStart = function () { return ({
    type: "SIGN_OUT_START"
}); };
export var signOutSuccess = function () { return ({
    type: "SIGN_OUT_SUCCESS"
}); };
export var signOutFailure = function (error) { return ({
    type: "SIGN_OUT_FAILURE",
    payload: error
}); };
export var signUpStart = function (signUpCredentials) { return ({
    type: "SIGN_UP_START",
    payload: signUpCredentials
}); };
export var signUpSuccess = function (user, additionalData) { return ({
    type: "SIGN_UP_SUCCESS",
    payload: { user: user, additionalData: additionalData }
}); };
export var signUpFailure = function (error) { return ({
    type: "SIGN_UP_FAILURE",
    payload: error
}); };
