export var FETCH_COLLECTIONS_START = "FETCH_COLLECTIONS_START";
export var FETCH_COLLECTIONS_SUCCESS = "FETCH_COLLECTIONS_SUCCESS";
export var FETCH_COLLECTIONS_FAILURE = "FETCH_COLLECTIONS_FAILURE";
export var fetchCollectionsStart = function () { return ({
    type: "FETCH_COLLECTIONS_START"
}); };
export var fetchCollectionsSuccess = function (collections) { return ({
    type: "FETCH_COLLECTIONS_SUCCESS",
    payload: collections
}); };
export var fetchCollectionsFailure = function (errorMsg) { return ({
    type: "FETCH_COLLECTIONS_FAILURE",
    payload: errorMsg
}); };
